import React, { Component } from 'react';
import cn from 'classnames';
import './item-status-filter.css';

export default class ItemStatusFilter extends Component {
  filters = [{ name: 'all', value: 'All' }, { name: 'active', value: 'Active' }, { name: 'done', value: 'Done' },]

  getClasses = (type) => {
    const { state } = this.props;
    return cn({
      'btn': true,
      'btn-primary': state === type,
      'btn-outline-secondary': state !== type,
    });
  };

  render() {
    const { toggleState } = this.props;

    const elements = this.filters.map(({ name, value }) => {
      return <button
                key={name}
                type="button"
                onClick={toggleState(name)}
                className={this.getClasses(name)} >
                  {value}
              </button>
    });
    return (
      <div className="btn-group" role="group">{elements}</div>
    );
  }
}